.App {
  text-align: center;
  background-color: #363636;
  margin: 0;
  padding: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.body {
  margin: 0;
}

.navbar-container {
  z-index: 2;
}

.carousel-container {
  z-index: 1;
}

.section-title {
  color: whitesmoke;
  font-size: 3rem;
  font-weight: bolder;

}

.our-work-container {
  padding: 20px;
}